import type { MouseEvent, TouchEvent } from 'react'

type Event = MouseEvent<HTMLElement> | TouchEvent<HTMLElement>

const isTouchEventGuard = (e: Event): e is TouchEvent<HTMLElement> => {
  return 'targetTouches' in e
}

export const createRipple = (e: Event): void => {
  const el = e.currentTarget

  if (el == null) {
    return
  }

  const rect = el.getBoundingClientRect()

  const circle = document.createElement('span')
  const diameter = Math.max(rect.width, rect.height)
  const radius = diameter / 2
  const pageX = isTouchEventGuard(e)
    ? e.targetTouches[0].pageX
    : e.pageX

  const pageY = isTouchEventGuard(e)
    ? e.targetTouches[0].pageY
    : e.pageY

  circle.style.width = `${diameter}px`
  circle.style.height = `${diameter}px`
  circle.style.left = `${pageX - rect.x - window.scrollX - radius}px`
  circle.style.top = `${pageY - rect.y - window.scrollY - radius}px`
  circle.classList.add('cf-ripple')

  const ripple = el.getElementsByClassName('cf-ripple')[0]

  if (ripple != null) {
    ripple.remove()
  }

  el.appendChild(circle)
}
